.properties{
    padding: 1rem;
}

.properties:hover > div > *:not(:hover){
    -webkit-transform: scale(0.94);
        -ms-transform: scale(0.94);
            transform: scale(0.94);
    opacity: 0.6;
    -webkit-transition: all 100ms ease-in;
    -o-transition: all 100ms ease-in;
    transition: all 100ms ease-in;
}


