.propertiesCard{
    margin: 10px;
    border-radius: 10px;
    overflow: hidden;
    -webkit-box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);
            box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);
    -webkit-transition: all 100ms ease-in;
    -o-transition: all 100ms ease-in;
    transition: all 100ms ease-in;
    cursor: pointer;
}

.propertiesCard:hover{
    -webkit-transform: scale(1.07);
        -ms-transform: scale(1.07);
            transform: scale(1.07);
}

.propertiesCard > img{
    -o-object-fit: fill;
       object-fit: fill;
    min-width: 300px;
    min-height: 200px;
    width: 100%;
}

.propertiesCard__info{
    margin-top: -9px;
    border-radius: 10px;
    padding: 20px;
    border: 1;
}

.propertiesCard__info > h2{
    font-size: 1.8rem;
    font-weight: 600;
}

.propertiesCard__info > h4{
    font-size: 1.4rem;
    font-weight: 300;
    margin: 8px 0;
}


