.header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    width: 100%;
}

.header__icon{
    -o-object-fit: contain;
       object-fit: contain;
    height: 10rem;
    margin-left: 2rem;
   

}

.header__center{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: auto;
    height: 3rem;
    padding: 1rem;
    margin: 0 6rem;
    border: 1px solid lightgrey;
    border-radius: 999px;
}

.header__center > input{
    -ms-flex:1 1;
        flex:1 1;
    border: none;
    outline: none;
}

.header__right{
    justify-self: flex-end;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.header__right > *{
    margin-right: 0.5rem;
}

.auth{
    margin-right: 1rem !important;
    color: #ff7779 !important;
    font-weight: 800 !important;
    background-color: lightgray !important;
}

.user__name{
    font-weight: 600;
    font-size: 1.2rem;
    color: #ff7779;
}


